@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow-x: hidden;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.is-shown {
  display: inline;
}

.is-hidden {
  display: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
}
.modal--close {
  display: none;
}

.modal > div {
  position: relative;
  background-color: #00b3ff;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  width: 500px;
  min-height: 610px;
  padding: 40px;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .modal > div {
    width: 100vw;
    padding: 40px 20px;
  }
}

.modal h2 {
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 42px;
  margin: 0;
}

.modal p {
  font-family: "PT Mono", "Arial", sans-serif;
  color: #ffffff;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  margin: 0 0 40px 0;
  width: 380px;
}
@media (max-width: 767px) {
  .modal p {
    font-size: 14px;
    line-height: 19px;
    margin: 0 0 35px 0;
    width: 90%;
  }
}

.modal__form-wrapper fieldset {
  padding: 0;
  margin: 0;
  border: none;
  display: flex;
  flex-direction: column;
}

.modal__label-wrapper input {
  font-family: "PT Mono", "Arial", sans-serif;
  color: #ffffff;
  border: none;
  width: 420px;
  height: 50px;
  background-color: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  padding: 10px 15px;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  margin: 0 0 20px 0;
}
.modal__label-wrapper input::placeholder {
  color: #ffffff;
  opacity: 0.4;
}
.modal__label-wrapper input:hover {
  background-color: rgba(4, 20, 39, 0.25);
}
@media (max-width: 767px) {
  .modal__label-wrapper input {
    width: 100%;
  }
}

.modal__label-wrapper--textarea textarea {
  font-family: "PT Mono", "Arial", sans-serif;
  border: none;
  width: 420px;
  min-height: 155px;
  background-color: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  padding: 22px 15px;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  margin: 0 0 20px 0;
  color: #ffffff;
}
.modal__label-wrapper--textarea textarea::placeholder {
  color: #ffffff;
  opacity: 0.4;
}
.modal__label-wrapper--textarea textarea:hover {
  background-color: rgba(4, 20, 39, 0.25);
}
@media (max-width: 767px) {
  .modal__label-wrapper--textarea textarea {
    width: 100%;
    padding: 25px 15px;
    min-height: 180px;
    margin-bottom: 10px;
  }
}

.modal__label-wrapper {
  position: relative;
}

.modal__form-wrapper button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  display: inline-block;
  vertical-align: baseline;
  padding: 14px 57px 12px 57px;
  max-width: 465px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  border: none;
  background-color: #041427;
  border-radius: 3px;
  cursor: pointer;
  user-select: none;
  margin: 0 0 15px 0;
}
@media (max-width: 767px) {
  .modal__form-wrapper button {
    max-width: 100%;
  }
}
.modal__form-wrapper button:hover {
  outline: 2px solid #041427;
  outline-offset: 2px;
}
.modal__form-wrapper button:active {
  outline: 2px solid #041427;
  outline-offset: 1px;
}

.modal > div > button {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 40px;
  height: 40px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.modal > div > button::after {
  content: "";
  display: block;
  width: 28px;
  height: 2px;
  border-radius: 5px;
  background-color: #ffffff;
  transform: translate(6px, -1px) rotate(45deg);
}
.modal > div > button::before {
  content: "";
  display: block;
  width: 28px;
  height: 2px;
  border-radius: 5px;
  background-color: #ffffff;
  transform: translate(6px, 1px) rotate(-45deg);
}
.modal > div > button:hover::after {
  background-color: #006c9b;
}
.modal > div > button:hover::before {
  background-color: #006c9b;
}
.modal > div > button:active::after {
  background-color: #041427;
}
.modal > div > button:active::before {
  background-color: #041427;
}

.modal__checkbox-mark {
  display: inline-block;
  vertical-align: baseline;
  position: absolute;
  border: 1px solid #ffffff;
  border-radius: 3px;
  width: 15px;
  height: 15px;
  top: 5px;
  left: 10px;
  cursor: pointer;
}

.modal__label-wrapper input:checked + .modal__checkbox-mark::after {
  content: "";
  display: block;
  width: 4px;
  height: 7px;
  border-bottom: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  transform: translate(4px, 1px) rotate(45deg);
}

.modal__label-wrapper input:focus + .modal__checkbox-mark {
  outline: 2px solid #041427;
}

.modal__checkbox-text {
  display: inline-block;
  vertical-align: baseline;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
  padding: 0 0 0 35px;
  cursor: pointer;
}
@media (max-width: 767px) {
  .modal__checkbox-text {
    top: 5px;
    left: -5px;
  }
}

.modal__label-wrapper:hover .modal__checkbox-text {
  opacity: 1;
}

