.logo__door {
  width: 300px;
  height: 300px;
  margin: 0 auto;
  background-color: #ffffff;
  border: 5px solid #000000;
  border-radius: 50%;
  position: relative;
}

.logo__mask {
  width: 180px;
  height: 300px;
  background-color: #ffffff;
  margin: -5px auto 0 auto;
}

.logo__inside {
  width: 5px;
  height: 120px;
  background-color: #000000;
  position: absolute;
  top: 15px;
  left: calc(50% - 2px);
  animation-name: door-swing;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-delay: 0;
  animation-fill-mode: forwards;
  transform-origin: 51% 108% 0;
  animation-timing-function: ease;
  transform: rotate(30deg);
}
.logo__inside::after {
  content: "";
  display: block;
  width: 5px;
  height: 120px;
  background-color: #000000;
  transform: rotate(60deg);
  position: absolute;
  top: 105px;
  right: 62px;
}
.logo__inside::before {
  content: "";
  display: block;
  width: 5px;
  height: 120px;
  background-color: #000000;
  transform: rotate(-60deg);
  position: absolute;
  top: 105px;
  left: 62px;
}

@keyframes door-swing {
  from {
    transform: rotate(30deg);
  }
  to {
    transform: rotate(-330deg);
  }
}
