    width: 23%;
    margin: 4px 0 0 0;
  }
}
@media (max-width: 767px) {
  .social__list {
    width: 48%;
    margin: 0 0 17px 0;
    justify-content: center;
  }
}

.social__item:not(:last-child) {
  margin-right: 6%;
}
@media (max-width: 1023px) {
  .social__item:not(:last-child) {
    margin-right: 10%;
  }
}
@media (max-width: 767px) {
  .social__item:not(:last-child) {
    margin-right: 9%;
  }
}

.social__item > a:hover {
  border-bottom: 2px solid #00b2ff;
}
.social__item > a:active {
  border-bottom: 4px solid #00b2ff;
}

.footer-navigation {
  width: 35.5%;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (max-width: 1023px) {
  .footer-navigation {
    display: block;
  }
}
@media (max-width: 767px) {
  .footer-navigation {
    width: 100%;
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
    position: relative;
  }
}

.footer-navigation > h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0 0 20px 0;
  grid-column: 1/-1;
}
@media (max-width: 767px) {
  .footer-navigation > h3 {
    padding: 20px 177px 20px 8%;
    margin: 0;
    cursor: pointer;
  }
  .footer-navigation > h3:focus {
    color: #00b2ff;
    outline: 2px solid #00b2ff;
  }
  .footer-navigation > h3:hover {
    color: #00b2ff;
  }
  .footer-navigation > h3:active {
    color: #74d5ff;
  }
}

@media (max-width: 767px) {
  .footer-navigation__title--closed::after {
    content: "";
    display: block;
    width: 15px;
    height: 1.5px;
    background-color: #c4c4c4;
    position: absolute;
    left: 86.7%;
    top: 25px;
  }
  .footer-navigation__title--closed::before {
    content: "";
    display: block;
    width: 15px;
    height: 1px;
    background-color: #c4c4c4;
    transform: rotate(90deg);
    position: absolute;
    left: 86.7%;
    top: 25px;
  }
}

@media (max-width: 767px) {
  .footer-navigation__title--opened::after {
    content: "";
    display: block;
    width: 15px;
    height: 1.5px;
    background-color: #c4c4c4;
    position: absolute;
    left: 86.7%;
    top: 25px;
  }
}

@media (max-width: 767px) {
  .footer-navigation__title--nojs::before {
    content: "";
    display: none;
  }
  .footer-navigation__title--nojs::after {
    content: "";
    display: none;
  }
}

.footer-navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 75.5%;
}
@media (max-width: 1023px) {
  .footer-navigation__list {
    flex-direction: column;
    width: 220px;
  }
}
@media (max-width: 767px) {
  .footer-navigation__list {
    padding: 0 25px 0 8%;
    margin: 0;
    width: 280px;
    display: none;
  }
}

@media (max-width: 767px) {
  .footer-navigation__list:last-child {
    margin: 0 0 15px 0;
  }
}

@media (max-width: 767px) {
  .footer-navigation__list--nojs {
    display: flex;
  }
}

@media (max-width: 767px) {
  .footer-navigation__list--opened {
    display: flex;
  }
}

.footer-navigation__item {
  min-width: 107%;
